	import slFilter from '@/components/sl-filter/sl-filter.vue';
    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default { 
		data() {
			return {
				storelist:[],
				sublist:[],
				act_info:[]
			}				
		},
		components: {slFilter},
        computed: mapState(['hasLogin','userInfo','Location']),  
		onLoad(e) {
			let _this = this;
			_this.city = _this.Location.city_name;
			console.log('页面加载');
			if (_this.hasLogin) { 
				_this.$Request.post('plat_act/act_detail',{id:e.act_id}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.act_info = res.datas.act_detail;
						_this.sublist = res.datas.sublist;
						_this.storelist = res.datas.storelist;
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
						
					}
				 });
			}
			//_this.loadData();
		},
         onReachBottom() {
             console.log('正在加载中...');
         },
         onPageScroll(){
             console.log('页面滚动...');
         },
         onPullDownRefresh() {
             console.log('上拉刷新...');
         },
        methods: {  
			toPage: function(url) {
				if (!this.hasLogin) {
					url="/pages/member/login";
				} 
				uni.navigateTo({
					url:url,
				})
				
			}

			 
        }  
    }  
